import { Component,OnInit, ChangeDetectionStrategy, ViewChild,TemplateRef} from '@angular/core';
import {
  startOfDay,
  endOfDay,
  subDays,
  addDays,
  endOfMonth,
  isSameDay,
  isSameMonth,
  addHours
} from 'date-fns';
import { Subject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {
  CalendarEvent,
  CalendarEventAction,
  CalendarEventTimesChangedEvent,
  CalendarView
} from 'angular-calendar';
import { viewParentEl } from '@angular/core/src/view/util';
import { Router } from '@angular/router';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { AppService } from 'src/app/app.service';
import { ToastrService } from 'ngx-toastr';
import * as $ from 'jquery';

const colors: any = {
  red: {
    primary: '#ad2121'
  },
  blue: {
    primary: '#1e90ff'
  },
  yellow: {
    primary: '#e3bc08'
  }
};

@Component({
  selector: 'app-admin-dashboard',
  changeDetection:ChangeDetectionStrategy.OnPush,
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.css']
})

export class AdminDashboardComponent implements OnInit{

  @ViewChild('modalContent') modalContent: TemplateRef<any>;

  view: CalendarView = CalendarView.Month;

  CalendarView = CalendarView;

  viewDate: Date = new Date();

  modalData: {
    action: string;
    event: CalendarEvent;
  };

  actions: CalendarEventAction[] = [];

  refresh: Subject<any> = new Subject();

  events: CalendarEvent[] = [];
  
  activeDayIsOpen: boolean = true;
 
    public title:String;
    public startDate:Date;
    public endDate:Date;
    public color:String;
    public purpose:String;
    public where:String;
    public userList:any;
    public eventList:any=[];
    public meetSetTo:String="all";

  constructor(private modal: NgbModal,public toastr: ToastrService,public Router: Router,public appService: AppService) {
    this.userListPut();
  }

  pensilUpdateMeeting(event){
    console.log(event);
    $("#addMeetingToList").css({"display":"block"});
    this.title=event.title;
    this.purpose=event.purpose;
    this.where=event.where;
    this.startDate=event.start;
    this.endDate=event.end;
    this.color=event.color.primary;
    let meetData = {
      title:this.title,
      purpose:this.purpose,
      where:this.where,
      startDate:this.startDate,
      endDate:this.endDate,
      color:this.color,
      meetId:event.meetId,
      setTo:"all"
    }
    console.log("updated data",meetData)
  }
  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      this.viewDate = date;
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
    }
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    event.start = newStart;
    event.end = newEnd;
    this.handleEvent('Dropped or resized', event);
    this.refresh.next();
  }

  handleEvent(action: string, event: CalendarEvent): void {
    this.modalData = { event, action };
    console.log(">>",this.modalData);
    this.modal.open(this.modalContent, { size: 'lg' });
  }
  
  addEvent(events): void {
    let data={setBy:'',start:startOfDay(new Date()),end:endOfDay(new Date()),title:'New event',color:colors.red}
    this.events.push({
      title: 'New event',
      start: startOfDay(new Date()),
      end: endOfDay(new Date()),
      color: colors.red,
      draggable: true,
      resizable: {
        beforeStart: true,
        afterEnd: true
      }
    });
    this.appService.saveMeeting(data);
    this.refresh.next();
  }

  removeEvent(index,e){
    this.events.splice(index, 1);
    this.refresh.next()
  }

  userListPut(){
    this.appService.getUserList().subscribe(
      (apiResponse) => {
        console.log(apiResponse);
        this.userList=apiResponse.data;
        console.log(this.userList);
        this.refresh.next();
      },
      (err) => {
        console.log("subscribe error");
      }
    )
  }//userListPut end here

  allInOne(){
    this.appService.getMeeting().subscribe(
      (apiResponse) => {
        if (apiResponse.status === 200) {
          let meetArray:any=apiResponse.data;
          for(let i=0;i<meetArray.length;i++){
            this.eventList.push({
              index:i,
              setBy:meetArray[i].setBy,
              setTo:meetArray[i].setTo,
              meetId:meetArray[i].meetId,
              title: meetArray[i].title,
              where:meetArray[i].where,
              actions: this.actions,
              color: {primary: meetArray[i].color, secondary: "#FAE3E3"},
              purpose:meetArray[i].purpose,
              start:new Date(meetArray[i].start),
              end:new Date(meetArray[i].end),
              startDate:new Date(meetArray[i].start).toLocaleDateString(),
              startTime:new Date(meetArray[i].start).toLocaleTimeString(),
              endDate:new Date(meetArray[i].end).toLocaleDateString(),
              endTime:new Date(meetArray[i].end).toLocaleTimeString()
            });
            this.refresh.next();
            // this.events.push(
            //   {
            //     start:new Date(meetArray[i].start),
            //     end:  new Date(meetArray[i].end),
            //     title: meetArray[i].title,
            //     color: {primary: meetArray[i].color, secondary: "#FAE3E3"},
            //     actions: this.actions,
            //     allDay: true,
            //     resizable: {
            //       beforeStart: true,
            //       afterEnd: true
            //     },
            //     draggable: true,
            //   }
            // );
            this.refresh.next();
          }
          this.events = this.eventList;
        } else {
          console.log("api res error");
        }
      },
      (err) => {
        console.log("subscribe error");
      }
    )
  }//allInOne End here

  createMeetingView(){
    $("#addMeetingToList").css({"display":"block"});
    $("#createMeetinButton").css({"display":"none"});
    $("#updateMeetingList").css({"display":"none"});
  }//createMeetingView end

  specificUser(user){
    window.scrollTo(0, 0);
    let fullName=user.firstName+" "+user.lastName;
    this.Router.navigate(['userCal', user.userId, fullName]);
  }//specificUser end

  updateMeetView(data){
    $("#createMeetinButton").css({"display":"block"});
    $("#addMeetingToList").css({'display':'none'});
    $("#updateMeetingList").css({'display':'block'});
    $("#meeting-setTo").val(data.setTo);
    $("#meeting-id").val(data.meetId);
    $("#ud-title").val(data.title);
    $("#ud-title").trigger("change")
    $("#ud-purpose").val(data.purpose);
    $("#ud-where").val(data.where);
    $("#ud-color").val(data.color.primary);
  }//updateMeetView end

  updateMeeting(){
    console.log("update meeting called");
    if(!$("#ud-title").val()){
      this.toastr.warning("Enter Title");
    }else if(!$("#ud-purpose").val()){
      this.toastr.warning("Enter Purpose");
    }else if(!$("#ud-where").val()){
      this.toastr.warning("Enter Location");
    }else if(!this.startDate){
      this.toastr.warning("Enter Start Date and Time");
    }else if(!this.endDate){
      this.toastr.warning("Enter End Date and Time");
    }else if(!$("#ud-color").val()){
      this.toastr.warning("Enter Color");
    }else{
      let meetData = {
        title:$("#ud-title").val(),
        purpose:$("#ud-purpose").val(),
        where:$("#ud-where").val(),
        startDate:this.startDate,
        endDate:this.endDate,
        color:$("#ud-color").val(),
        meetId:$("#meeting-id").val(),
        setTo:$("#meeting-setTo").val()
      }
      console.log("meetDate",meetData);
      this.appService.updateMeeting(meetData).subscribe(
        (apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("Meeting Updated Successfull");
            this.meetSetTo="all";
            this.refresh.next();
            location.reload();
          } else {
            this.toastr.error(apiResponse.message);
          }
        },
        (err) => {
          this.toastr.error('Some error occured');
        }
      )
    }
  }//updateMeeting end

  deleteMeet(event,index){
    console.log("event",event);
    if(event.meetId){
      this.appService.deleteMeeting(event).subscribe(
        (apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("Meeting Deleted Successfull");
            this.eventList.splice(index, 1);
            this.refresh.next();
          } else {
            this.toastr.error(apiResponse.message);
          }
        },
        (err) => {
          this.toastr.error('Some error occured');
        }
      )
    }else{
      this.toastr.warning("Error In Deleting Meeting");
    }
  }//deleteMeet end

  addMeeting(){
    if(!this.title){
      this.toastr.warning("Enter Title");
    }else if(!this.purpose){
      this.toastr.warning("Enter Purpose");
    }else if(!this.where){
      this.toastr.warning("Enter Location");
    }else if(!this.startDate){
      this.toastr.warning("Enter Start Date and Time");
    }else if(!this.endDate){
      this.toastr.warning("Enter End Date and Time");
    }else if(!this.color){
      this.toastr.warning("Enter Color");
    }else{
      let meetData = {
        title:this.title,
        purpose:this.purpose,
        where:this.where,
        startDate:this.startDate,
        endDate:this.endDate,
        color:this.color,
        setTo:"all",
        email:Cookie.get("email")
      }
      console.log("meetData",meetData);
      this.appService.saveMeeting(meetData).subscribe(
        (apiResponse) => {
          if (apiResponse.status === 200) {
            this.toastr.success("Meeting Added Successfull");
            this.meetSetTo="all";
            this.refresh.next();
            setTimeout(() => {
              location.reload();
            }, 2000);
          } else {
            this.toastr.error(apiResponse.message);
          }
        },
        (err) => {
          this.toastr.error('Some error occured');
        }
      )
    } 
  }//addMeeting end

  ngOnInit(){
    this.allInOne();
    let getUserInfoFromLocalStorage:any = ()=>{
      let role=Cookie.get("role");
      let authToken=Cookie.get("authtoken");
      if(role == "user"){
        this.Router.navigate(['/mDashboard']);
      }else if(authToken == null){
        this.Router.navigate(['/']);
      }
    }//getUserInfoFromLocalStorage
    getUserInfoFromLocalStorage();
  }//ngOnInit end
}//class AdminDashboardComponent end