import { ActivatedRoute } from '@angular/router';
import { Component,OnInit, ChangeDetectionStrategy, ViewChild,TemplateRef} from '@angular/core';
import {
  startOfDay,
  endOfDay,
  subDays,
  addDays,
  endOfMonth,
  isSameDay,
  isSameMonth,
  addHours
} from 'date-fns';
import { Subject } from 'rxjs';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {
  CalendarEvent,
  CalendarEventAction,
  CalendarEventTimesChangedEvent,
  CalendarView
} from 'angular-calendar';
import { viewParentEl } from '@angular/core/src/view/util';
import { Router } from '@angular/router';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { AppService } from 'src/app/app.service';
import { ToastrService } from 'ngx-toastr';
import * as $ from 'jquery';
const colors: any = {
  red: {
    primary: '#ad2121'
  },
  blue: {
    primary: '#1e90ff'
  },
  yellow: {
    primary: '#e3bc08'
  }
};
@Component({
  selector: 'app-user-cal',
  changeDetection:ChangeDetectionStrategy.OnPush,
  templateUrl: './user-cal.component.html',
  styleUrls: ['./user-cal.component.css']
})
export class UserCalComponent implements OnInit {
  @ViewChild('modalContent') modalContent: TemplateRef<any>;

  view: CalendarView = CalendarView.Month;

  CalendarView = CalendarView;

  viewDate: Date = new Date();

  modalData: {
    action: string;
    event: CalendarEvent;
  };

  actions: CalendarEventAction[] = [
    {
      label: '<i class="fa fa-fw fa-pencil"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        console.log(">>>",event);
        this.handleEvent('Edited', event);
      }
    },
    {
      label: '<i class="fa fa-fw fa-times"></i>',
      onClick: ({ event }: { event: CalendarEvent }): void => {
        this.events = this.events.filter(iEvent => iEvent !== event);
        this.handleEvent('Deleted', event);
      }
    }
  ];

  refresh: Subject<any> = new Subject();

  events: CalendarEvent[] = [];
  
  activeDayIsOpen: boolean = true;
  public calUserName="";
  public userId:any="";
  public title:String;
  public startDate:Date;
  public endDate:Date;
  public color:String;
  public purpose:String;
  public where:String;
  public userList:any;
  public eventList:any=[];
  public meetSetTo:String="all";
  constructor(private modal: NgbModal,public activeRoute:ActivatedRoute,public toastr: ToastrService,public Router: Router,public appService: AppService) { }

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      this.viewDate = date;
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
    }
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    event.start = newStart;
    event.end = newEnd;
    this.handleEvent('Dropped or resized', event);
    this.refresh.next();
  }

  handleEvent(action: string, event: CalendarEvent): void {
    this.modalData = { event, action };
    console.log(">>",this.modalData);
    this.modal.open(this.modalContent, { size: 'lg' });
  }
  createMeetingView(){
    $("#addMeetingToList").css({"display":"block"});
    $("#createMeetinButton").css({"display":"none"});
    $("#updateMeetingList").css({"display":"none"});
  }

  updateMeetView(data){
    console.log("update=>>",data);
      $("#createMeetinButton").css({"display":"block"});
      $("#addMeetingToList").css({'display':'none'});
      $("#updateMeetingList").css({'display':'block'});
      $("#meeting-id").val(data.meetId);
      $("#ud-title").val(data.title);
      $("#ud-title").trigger("change")
      $("#ud-purpose").val(data.purpose);
      $("#ud-where").val(data.where);
      $("#ud-color").val(data.color.primary);
    }
    
  deleteMeet(id,index){
    console.log(id,index);
    if(id){
      this.appService.deleteMeeting(id).subscribe(
        (apiResponse) => {
          console.log(apiResponse);
          if (apiResponse.status === 200) {
            this.toastr.success("Meeting Deleted Successfull");
            this.eventList.splice(index, 1);
            
            this.refresh.next();
            // location.reload();
          } else {
            this.toastr.error(apiResponse.message);
          }
        },
        (err) => {
          this.toastr.error('Some error occured');
        }
      )
    }else{
      this.toastr.warning("Error In Deleting Meeting");
    }
  }
  
  addMeeting(){
    if(!this.title){
      this.toastr.warning("Enter Title");
    }else if(!this.purpose){
      this.toastr.warning("Enter Purpose");
    }else if(!this.where){
      this.toastr.warning("Enter Location");
    }else if(!this.startDate){
      this.toastr.warning("Enter Start Date and Time");
    }else if(!this.endDate){
      this.toastr.warning("Enter End Date and Time");
    }else if(!this.color){
      this.toastr.warning("Enter Color");
    }else{
      let meetData = {
        title:this.title,
        purpose:this.purpose,
        where:this.where,
        startDate:this.startDate,
        endDate:this.endDate,
        color:this.color,
        setTo:this.userId
      }
      console.log("addusermeet->",meetData);
      this.appService.saveMeeting(meetData).subscribe(
        (apiResponse) => {
          console.log("<<=",apiResponse);
          if (apiResponse.status === 200) {
            this.toastr.success("Meeting Added Successfull");
            this.meetSetTo="all";
            this.refresh.next();
            setTimeout(() => {
              location.reload();
            }, 2000);
          } else {
            this.toastr.error(apiResponse.message);
          }
        },
        (err) => {
          this.toastr.error('Some error occured');
        }
      )
    }
    
  }

  ngOnInit() {
    let urlUserId=this.activeRoute.snapshot.paramMap.get('userId');
    this.userId=urlUserId;
    let urlFullName=this.activeRoute.snapshot.paramMap.get('fullName');
    this.calUserName=urlFullName;
    console.log("userid",this.userId);
        this.appService.getUserMeeting(urlUserId).subscribe(
        (apiResponse) => {
          console.log(apiResponse);
          if (apiResponse.status === 200) {
            let meetArray:any=apiResponse.data;
            for(let i=0;i<meetArray.length;i++){
              console.log("i",i);
              this.eventList.push({
                setBy:meetArray[i].setBy,
                meetId:meetArray[i].meetId,
                title: meetArray[i].title,
                where:meetArray[i].where,
                color: {primary: meetArray[i].color, secondary: "#FAE3E3"},
                purpose:meetArray[i].purpose,
                start:new Date(meetArray[i].start),
                end:new Date(meetArray[i].end),
                startDate:new Date(meetArray[i].start).toLocaleDateString(),
                startTime:new Date(meetArray[i].start).toLocaleTimeString(),
                endDate:new Date(meetArray[i].end).toLocaleDateString(),
                endTime:new Date(meetArray[i].end).toLocaleTimeString()
              });
              this.refresh.next();
            }
            this.events=this.eventList;            
          } else {
            this.toastr.error(apiResponse.message);
          }
        },
        (err) => {
          this.toastr.error('Some error occured');
        }
      )
  }

}
